import React, { Component } from 'react';

class ThankYou extends Component {
  static async getInitialProps(props) {
    return {code: props.query}
  }
  render() {
    return (
      <h1>Thank you! {this.props.code}</h1>
    );
  }
}

export default ThankYou;



